export default class MainPageImagesController {
    public static async getImages(url: string, username: string, token: string) {
        try {
            console.log('MainPageImagesController:getImages:', url, username, token);
            const header: any = {
                method: 'GET',
                mode: 'cors',
                cache: 'no-cache', // *default, no-cache, reload, force-cache, only-if-cached
                credentials: 'same-origin', // include, *same-origin, omit
                headers: {
                    // 'Access-Control-Allow-Origin': '*',
                    // 'Access-Control-Request-Headers': 'X-Custom-Header',
                    // 'Access-Control-Allow-Credentials': 'true',
                    'Content-Type': 'application/json;charset=utf-8',
                    // 'Authorization': `Basic ${btoa(`${username}:${token}`)}`,
                    // 'Host': 'http://localhost:8081/',
                    // 'Accept': '*/*',
                    // 'Connection': 'keep-alive',
                    // 'cache': 'no-cache',
                    // 'Accept-Encoding': 'gzip, deflate',

                },
            }
            const response: any = await fetch(url, header);
            const data: any = await response.text(); // json();
            return (data !== '') ? data : 'Nothing';
        } catch (err) {
            console.log(err);
        }
    }
}
