










import { Component, Vue } from 'vue-property-decorator';
import MainPageImagesController from './controllers/MainPageImages/MainPageImagesController';

@Component({
  components: {
  },
})

export default class App extends Vue {
    private result: string = 'Привет++';
    private url: string = '';
    private onClick( e: any) {
      console.log(this.url = e.target.options[e.target.selectedIndex].text);
      this.getData();
    }

    private async getData() {
      const username: string = 'gerasim';
      const token: string = '?';
      const result: any = '';
      try {
        this.result = await MainPageImagesController.getImages(this.url, username, token);
      } catch (error) {
        this.result = error;
      }
      console.log('getData:', this.result);      
    }

    private async mounted() {
      console.log('mounted');
      const selector:any = this.$refs.urlselector;
      this.url = selector.options[selector.selectedIndex].text;
      console.log('selectedUrl:',this.url);
      this.getData();
    }
}

